import { Power2, TimelineLite } from 'gsap';

export default function light(Dom_light, Dom_title, id_contract) {
  const tl = new TimelineLite(),
    dom_contract = document.getElementById(id_contract);
  let vw = window.innerWidth,
    vh = window.innerHeight;


  tl
    .set(Dom_light, {
      attr: {
        r: vh * 0.22
      },
    })
    .to(Dom_light, 1, {
      force3D: false,
      y: vh * (0.2 + 0.3 / 2 + 0.02),
      // attr: {
      //   cy: vh * (0.2 + 0.3 / 2 + 0.02), //0.05: adjust
      //   r: vh * 0.22
      // },
      ease: Power2.easeOut
    })
    .to(Dom_light, 1, {
      force3D: false,
      y: 16 * (62.2 + 3.48 / 2) + 0.15 * vh,
      // attr: {
      //   cy: 16 * (62.2 + 3.48 / 2) + 0.15 * vh,
      //   r: vw * 0.3
      // },
      delay: 1
    })
    .to(Dom_light, 1, {
      force3D: false,
      transformOrigin: "center center",
      skewX: 40,
      rotation: -25,
    // attr: {
    //   r: vw * 0.3
    // }
    })
    .to(Dom_title, 1, {
      opacity: 1,
      ease: Power2.easeOut
    })
}



